import React from "react";

export const NOOP = () => null;

export const preventDefault = (
  e: React.MouseEvent<HTMLElement, MouseEvent>
) => {
  e.preventDefault();
  e.stopPropagation();
};

/**
 * Returns a valid HTML tag which can be used for building components.
 * @param tagName
 */
export const getValidTagName = (tagName?: keyof HTMLElementTagNameMap) => {
  let Tag = "div";
  if (
    tagName !== undefined &&
    tagName !== "applet" &&
    tagName !== "basefont" &&
    tagName !== "dir" &&
    tagName !== "font" &&
    tagName !== "frame" &&
    tagName !== "frameset" &&
    tagName !== "marquee" &&
    tagName !== "slot"
  ) {
    Tag = tagName;
  }
  return Tag;
};

/**
 * Prepend a character to string till it reaches a specified length
 * @param str isRequired, string
 * @param pad optional (2), length of final string
 * @param char optional ('0'), character to be prepended to attain specified length.
 * @returns string
 */
export const addCharToString = (str: string, pad = 2, char = "0") => {
  while (str.length < pad) str += char;
  return str;
};

/**
 * Get date-time string in FA format
 * @param date Unix time in seconds (number)
 * @param format One of "dateTime" | "date" | "time"
 * @returns YYYY-MM-DD  HH:mm:ss
 */
export const readableDateTimeFormat = (
  date: Date,
  format: "dateTime" | "date" | "time" = "dateTime"
) => {
  const readableDate = (date: Date) => {
    const YYYY = addCharToString(date.getFullYear().toString(), 4);
    const MM = addCharToString((date.getMonth() + 1).toString());
    const DD = addCharToString(date.getDate().toString());
    return `${YYYY}-${MM}-${DD}`;
  };
  const readableTime = (date: Date) => {
    const HH = addCharToString(date.getHours().toString());
    const mm = addCharToString(date.getMinutes().toString());
    const ss = addCharToString(date.getSeconds().toString());
    return `${HH}:${mm}:${ss}`;
  };
  switch (format) {
    case "date":
      return readableDate(date);
    case "time":
      return readableTime(date);
    default:
      return `${readableDate(date)} ${readableTime(date)}`;
  }
};

/**
 * Insert/update a query parameters to the URL without reloading.
 * @param {string} key
 * @param {string} value
 */
export const insertQueryParam = (key: string, value: string) => {
  const encodedValue = encodeURIComponent(value);
  const hashUrl = window.location.hash.split("?")[0];
  const queryUrl = window.location.hash.split("?")[1];
  const baseURL =
    window.location.protocol +
    "//" +
    window.location.host +
    window.location.pathname +
    hashUrl;
  let paramsMap: string[][] = [];
  let match = false;
  if (queryUrl) {
    paramsMap = queryUrl
      .split("&")
      .map((query: string) => [query.split("=")[0], query.split("=")[1]]);

    paramsMap.forEach((query: string[]) => {
      if (query[0] === key) {
        query[1] = encodedValue;
        match = true;
      }
    });
  }
  if (!match) paramsMap.push([key, encodedValue]);

  const newQueryUrl = paramsMap
    .map((query: string[]) => query.join("="))
    .join("&");

  if (window.history.replaceState) {
    const newUrl = baseURL + "?" + newQueryUrl;
    window.history.replaceState({ path: newUrl }, "", newUrl);
  }
};
