import React from "react";
import styled from "@emotion/styled";
import {
  Text,
  Button,
  Divider,
  Box,
  getTextDimensions,
  Theme,
} from "@faharmony/core";
import { INavHeaderProps } from "./types";

const StyledHeader = styled.header<INavHeaderProps>`
  width: inherit;
  background: transparent;
  overflow: hidden;
  padding: ${(props) =>
      props.isMenuCollapsed ? Theme.spacing() : Theme.spacing("lg")}
    ${() => Theme.spacing()} 0;

  .heading {
    margin: 0 ${() => Theme.spacing()} ${() => Theme.spacing()};
    width: calc(inherit - 2 * ${Theme.spacing()});
    overflow: hidden;
  }

  > .navHeader-actions {
    margin-bottom: ${() => Theme.spacing()};
  }
`;

/**
 * Navigation Header
 * ---
 * Builds the navigation Header containing heading
 * and actions.
 *
 * @author Siddhant Gupta <siddhant@fasolutions.com>
 */
const Header = (props: INavHeaderProps) => {
  const { heading, shortHeading, actions, isMenuCollapsed } = props;
  // Check whether to use short or normal heading
  const showShortHeading =
    getTextDimensions(heading, "h1").width > 210 && !!shortHeading;

  const actionButtons = (list: INavHeaderProps["actions"]) => (
    <Box
      justifyContent="space-between"
      wrap="nowrap"
      direction={isMenuCollapsed ? "column" : "row"}
      className="navHeader-actions"
    >
      {list?.map((action) => (
        <Button {...action} key={action.id} />
      ))}
    </Box>
  );

  return isMenuCollapsed ? (
    actions && !!actions.length ? (
      <StyledHeader {...props}>
        {actionButtons(actions)}
        <Divider spacing="none" />
      </StyledHeader>
    ) : null
  ) : (
    <StyledHeader {...props}>
      <div className="heading">
        <Text
          truncate
          variant={"h1"}
          value={showShortHeading ? shortHeading || "" : heading}
        />
      </div>
      {actions && !!actions.length && actionButtons(actions)}
      <Divider spacing="none" />
    </StyledHeader>
  );
};

export default Header;
