import { useState, useEffect } from "react";
import { i18n } from "i18next";
import {
  faGlobe,
  faUser,
  faSignOut,
  faLightbulbOn,
  faLightbulbSlash,
  faUserEdit,
  faLifeRing,
} from "@fortawesome/pro-light-svg-icons";
import {
  languageNativeNames,
  LanguageCode,
  getCurrentLanguageCode,
  setCurrentLanguageCode,
  getTranslationLanguageCodes,
  IPopoverMenuItemProps,
  useKeycloak,
  setTheme,
  getCurrentDarkModePreference,
  useKeyPress,
  keyPressCombos,
  getKeyPressComboString,
} from "@faharmony/core";
import { IFABarProps } from "../views";

const useLanguageSwitcher = (i18n?: i18n): IFABarProps["actions"] => {
  const [i18nextLng, setI18nextLng] = useState<LanguageCode>(
    getCurrentLanguageCode() || i18n?.language || "en"
  );
  if (i18n) {
    const menuItems = getTranslationLanguageCodes(i18n).map(
      (code): IPopoverMenuItemProps => ({
        id: code,
        label: languageNativeNames[code]?.label || "English",
        disabled: code === i18nextLng,
        onClick: () => {
          i18n && i18n.changeLanguage(code);
          setCurrentLanguageCode(code);
          setI18nextLng(code);
        },
      })
    );
    const tooltip =
      languageNativeNames[i18nextLng]?.changeLabel || "Change language";
    // menuItems.unshift({ id: "title", divider: true, label: tooltip });
    return [
      {
        id: "language",
        icon: faGlobe,
        tooltip,
        popoverMenuItems: menuItems,
      },
    ];
  }
  return [];
};

const useUserAction = (devMode: boolean = false): IFABarProps["actions"] => {
  const { keycloakInstance } = useKeycloak();
  if (devMode) return [];
  return [
    {
      id: "userManager",
      icon: faUser,
      tooltip: "Manage user account",
      popoverMenuItems: [
        {
          id: "userAccount",
          label: "Manage user account",
          icon: faUserEdit,
          onClick: () =>
            window.open(keycloakInstance.createAccountUrl(), "_blank"),
        },
        {
          id: "logout",
          icon: faSignOut,
          label: "Logout",
          onClick: () => keycloakInstance.logout(),
        },
      ],
    },
  ];
};

const useThemeAction = () => {
  const [darkMode, setDarkMode] = useState(getCurrentDarkModePreference);
  useEffect(() => {
    setTheme({ enableDarkMode: darkMode });
  }, [darkMode]);
  const isKeyPressed = useKeyPress(keyPressCombos.switchDarkMode);
  useEffect(() => {
    if (isKeyPressed) setDarkMode((p) => !p);
  }, [isKeyPressed]);
  return {
    id: "darkMode",
    icon: darkMode ? faLightbulbOn : faLightbulbSlash,
    tooltip: `Switch to ${
      darkMode ? "light" : "dark"
    } mode [${getKeyPressComboString(keyPressCombos.switchDarkMode)}]`,
    onClick: () => setDarkMode((p) => !p),
  };
};

const useStaticActions = (): IFABarProps["actions"] => {
  return [
    {
      id: "helpDesk",
      icon: faLifeRing,
      tooltip: "Help desk",
      onClick: () =>
        window.open(`https://support.fasolutions.com/hc/en-us/`, "_blank"),
    },
  ];
};

const useGlobalActions = (
  i18n?: i18n,
  devMode?: boolean
): IFABarProps["actions"] => {
  const languageSwitchAction = useLanguageSwitcher(i18n);
  const userAction = useUserAction(devMode);
  const themeAction = useThemeAction();
  const staticActions = useStaticActions();
  return [
    // Other actions without logic
    ...staticActions,
    // Action to manage darkMode/theme
    themeAction,
    // Add language switcher
    ...languageSwitchAction,
    // Add user action menu
    ...userAction,
  ];
};

export default useGlobalActions;
