import React from "react";
import Div100vh from "react-div-100vh";
import {
  Divider,
  Grid,
  Text,
  Breadcrumbs,
  Theme,
  useResponsive,
} from "@faharmony/core";
import { IPageViewProps } from "./types";
import { StyledBody, StyledFooter, StyledHeader, StyledView } from "./style";
import { TabSwitcher, useCurrentTab } from "./Tabs";

const PageViewHeader = (
  props: IPageViewProps & { currentTabId: string | null }
) => {
  const {
    headerContent,
    heading,
    actions,
    caption,
    tabs,
    currentTabId,
  } = props;
  const tabSwitcher =
    tabs && tabs.length > 1 ? (
      <TabSwitcher tabs={tabs} currentTabId={currentTabId} />
    ) : (
      <div style={{ padding: `0 ${Theme.spacing()}` }}>
        <Divider />
      </div>
    );
  return (
    <StyledHeader id="pageView-header" rows={headerContent ? 3 : 2}>
      <Grid
        gridTemplate="auto / auto max-content"
        overflow="inherit"
        style={{ padding: `0 ${Theme.spacing()}` }}
      >
        <div
          id="pageView-header-title"
          style={{ marginTop: caption ? 0 : Theme.spacing() }}
        >
          {caption ? (
            typeof caption === "string" ? (
              <Text
                value={caption.toString()}
                variant="strong"
                color="secondary"
              />
            ) : (
              <Breadcrumbs crumbs={caption} variant="strong" />
            )
          ) : null}
          <Text value={heading} variant="h2" truncate />
        </div>
        {actions && <div id="pageView-header-actions">{actions}</div>}
      </Grid>
      {headerContent && <div id="pageView-header-content">{headerContent}</div>}
      {/* Tabs component contains its own divider  */}
      {tabSwitcher}
    </StyledHeader>
  );
};

const PageViewFooter = (props: IPageViewProps) => (
  <StyledFooter>
    <Divider />
    <div id="pageView-footer-content">{props.footerContent}</div>
  </StyledFooter>
);

const PageViewBody = ({
  currentTabId,
  tabs = [],
}: {
  currentTabId: string | null;
  tabs: IPageViewProps["tabs"];
}) => {
  const visibleContent: React.ReactNode = React.useMemo(
    () =>
      currentTabId
        ? tabs.find((tab) => tab.id === currentTabId)?.content
        : tabs[0].content,
    [currentTabId, tabs]
  );
  return <StyledBody id="pageView-body">{visibleContent}</StyledBody>;
};

/**
 * PageView
 * ---
 * Page View is the primary view which is used as base for all pages in FA App.
 *
 * It should NOT be used as base for Modals. See SummaryView/ContactView for that.
 *
 * @author Siddhant Gupta <siddhant@fasolutions.com>
 * @version 1.5.0
 * @example
 * <PageView headerTitle="Heading"> {content} </PageView>
 */
const PageView = (props: IPageViewProps) => {
  const { isMobile } = useResponsive();
  const { tabs, currentTabId } = useCurrentTab(props);

  return (
    <Div100vh style={{ height: isMobile ? "calc(100rvh - 52px)" : "100rvh" }}>
      <StyledView className="pageView" hasFooter={!!props.footerContent}>
        <PageViewHeader {...props} currentTabId={currentTabId} tabs={tabs} />
        <PageViewBody tabs={tabs} currentTabId={currentTabId} />
        {props.footerContent && <PageViewFooter {...props} />}
      </StyledView>
    </Div100vh>
  );
};

export { PageView, IPageViewProps };
export default PageView;
