import {
  faServer,
  faAnalytics,
  faShieldAlt,
  faKey,
  faPalette,
} from "@fortawesome/pro-light-svg-icons";
import { useKeycloak } from "@faharmony/core";
import { IFABarProps } from "../views";

const faApps: IFABarProps["apps"] = [
  {
    id: "fa-back",
    appName: "FA Back",
    icon: faServer,
    url: "/main/",
  },
  {
    id: "fa-front",
    appName: "FA Front",
    icon: faAnalytics,
    url: "/front/",
  },
  {
    id: "fa-admin-app",
    appName: "FA Admin Console",
    icon: faShieldAlt,
    url: "/admin/",
  },
  {
    id: "fa-auth-console",
    appName: "FA Auth Console",
    icon: faKey,
    url: "/auth/admin/fa/console/#/realms/fa/",
    openInNewWindow: true,
  },
];
const harmonyApp: IFABarProps["apps"] = [
  {
    id: "fa-harmony",
    appName: "FA Harmony",
    icon: faPalette,
    url: "/harmony/",
  },
];

const useAppSwitcher = (devMode: boolean = false): IFABarProps["apps"] => {
  const { keycloakInstance, isAdmin } = useKeycloak();

  // If app is running in devMode
  if (devMode) return harmonyApp;

  // Return all apps id user is Admin
  if (isAdmin) return faApps;

  // Return selected apps to in which user has at least one role.
  const clients = Object.entries(keycloakInstance.resourceAccess || {}).map(
    ([resource]) => resource
  );
  const apps: IFABarProps["apps"] = [];
  faApps.forEach((app) => {
    if (clients.some((client) => client === app.id)) apps.push(app);
  });
  return apps;
};

export default useAppSwitcher;
