import React, { useEffect } from "react";
import {
  Box,
  Theme,
  Text,
  getTextDimensions,
  Tooltip,
  useResizeObserver,
  useDebounceValue,
  Popover,
  Button,
} from "@faharmony/core";
import { useHistory, useRouteMatch, useLocation } from "react-router-dom";
import { IPageViewTabProps } from "./types";
import { StyledTabButton, tabButtonWidth } from "./style";
import { faCaretDown } from "@fortawesome/pro-light-svg-icons";
import { IPageViewProps } from ".";

export const useCurrentTab = ({ tabs = [], children }: IPageViewProps) => {
  const { url } = useRouteMatch();
  const history = useHistory();
  const location = useLocation();
  const [currentTabId, setId] = React.useState<string | null>(null);

  useEffect(() => {
    const tabId = new URLSearchParams(location.search).get("tab");
    if (tabs.length === 0) {
      tabs.push({
        id: "",
        heading: "",
        content: children || (
          <div id="pageView-noContent">
            <Text value="No content available." color="secondary" />
          </div>
        ),
      });
      if (tabId) history.push(url);
    } else if (tabs.findIndex((tab) => tab.id === tabId) < 0) {
      history.push(`${url}?tab=${tabs[0].id}`);
    }
    setId(tabId);
  }, [tabs, location]);

  return { currentTabId, tabs };
};

const TabButton = ({
  tab: { id, heading },
  isActive,
}: {
  tab: IPageViewTabProps;
  isActive: boolean;
}) => {
  const { url } = useRouteMatch();
  const history = useHistory();

  const showTooltip =
    getTextDimensions(heading, "h4").width > tabButtonWidth - 20;

  const button = (
    <StyledTabButton
      id={"tab-" + id}
      key={id}
      isActive={isActive}
      onClick={() => history.push(`${url}?tab=${id}`)}
    >
      <Text value={heading} variant="h4" truncate />
    </StyledTabButton>
  );

  return showTooltip ? (
    <Tooltip value={heading} placement="bottom">
      {button}
    </Tooltip>
  ) : (
    button
  );
};

export const TabSwitcher = ({
  tabs,
  currentTabId,
}: {
  tabs: IPageViewTabProps[];
  currentTabId: string | null;
}) => {
  const { url } = useRouteMatch();
  const history = useHistory();

  const { ref: switcherRef, width = 1 } = useResizeObserver<HTMLDivElement>();
  const switcherWidth = useDebounceValue(width, 100);

  const visibleTabCount = Math.floor(switcherWidth / tabButtonWidth);

  const visibleTabs = tabs.slice(0, visibleTabCount);
  const hiddenTabs = tabs.slice(visibleTabCount);

  // Make active tab visible
  const activeTabHiddenIndex = hiddenTabs.findIndex(
    (tab) => tab.id === currentTabId
  );
  if (activeTabHiddenIndex > -1) {
    const lastVisibleTab = visibleTabs.pop();
    visibleTabs.push(hiddenTabs[activeTabHiddenIndex]);
    if (lastVisibleTab) hiddenTabs.unshift(lastVisibleTab);
  }

  return (
    <Box
      spacing="xl"
      style={{ borderBottom: `1px solid ${Theme.colorOutline()}` }}
      overflow="hidden"
      ref={switcherRef}
      width="auto"
    >
      {visibleTabs.map((tab) => (
        <TabButton key={tab.id} tab={tab} isActive={currentTabId === tab.id} />
      ))}
      {hiddenTabs.length > 0 && (
        <div style={{ position: "absolute", right: Theme.spacing() }}>
          {
            <Popover.Menu
              menuItems={hiddenTabs
                .filter((tab) => tab.id !== currentTabId)
                .map(({ id, heading }) => ({
                  id,
                  label: heading,
                  onClick: () => history.push(`${url}?tab=${id}`),
                }))}
            >
              <Button
                icon={faCaretDown}
                tooltip="More tabs"
                spacing="xs"
                variant="secondary"
              />
            </Popover.Menu>
          }
        </div>
      )}
    </Box>
  );
};
