import React from "react";
import { Column, Hooks } from "react-table";
import ITableViewProps from "../types";

import Checkbox from "../components/Checkbox";
import SingleRowActionMenu from "../components/SingleRowActionMenu";

export const systemColumnsIds = ["rowActions", "selection", "expander"];

export const addColumns = <T extends object>(
  hooks: Hooks<T>,
  props: ITableViewProps<T>
) => {
  hooks.visibleColumns.push((columns) => {
    const startColumns: Column<T>[] = [];
    const endColumns: Column<T>[] = [];

    // Add row to show checkbox for multi-row-select
    props.enableMultipleRowSelect &&
      startColumns.push({
        id: "selection",
        width: 40,
        disableSortBy: true,
        disableFilters: true,
        className: "fixedColumnStart",
        // eslint-disable-next-line react/display-name
        Header: (row) => <Checkbox {...row.getToggleAllRowsSelectedProps()} />,
        // eslint-disable-next-line react/display-name
        Cell: ({ row }: any) => (
          <Checkbox {...row.getToggleRowSelectedProps()} />
        ),
      });

    // Add column to show actions dropdown
    props.singleRowActions &&
      endColumns.push({
        id: "rowActions",
        width: 40,
        disableSortBy: true,
        disableFilters: true,
        className: "fixedColumnEnd",
        // eslint-disable-next-line react/display-name
        Cell: ({ row }: any) => (
          <SingleRowActionMenu row={row} tableProps={props} />
        ),
      });

    return [...startColumns, ...columns, ...endColumns];
  });
};

export default addColumns;
