import React from "react";
import { v1 as uuid } from "uuid";
import { merge } from "lodash";
import Highcharts, { Chart } from "highcharts";
import HighchartsReact from "highcharts-react-official";
import { useResizeObserver } from "@faharmony/core";

import { useChartOptions } from "../useChartOptions";
import { FAChartProps } from "../type";
import { StyledChart } from "../style";

type BarProps = FAChartProps;

const customOptions = { chart: { type: "bar" } };

export const Bar = (props: BarProps) => {
  const chartRef = React.useRef<HTMLDivElement>(null);
  const [chart, setChart] = React.useState<Chart>();
  const options = merge(useChartOptions(props.options), customOptions);
  const callback = (newChart: Chart) => {
    setChart(newChart);
    props.callback && props.callback(newChart);
  };

  const defaultContainerProps = { id: uuid() };
  useResizeObserver({
    ref: chartRef,
    onResize: () => {
      if (chart) chart.setSize(null, null);
    },
  });
  return (
    <StyledChart headless={props.headless} ref={chartRef}>
      <div className="header" />
      <HighchartsReact
        highcharts={Highcharts}
        options={options}
        containerProps={props.containerProps || defaultContainerProps}
        callback={callback}
        constructorType={"chart"}
      />
    </StyledChart>
  );
};

export default Bar;
