import React from "react";
import { faChevronDown, faChevronUp } from "@fortawesome/pro-light-svg-icons";
import { Text, Icon, Button } from "@faharmony/core";

import { useExpandItem, useSubItemSelected } from "../helpers";
import { INavPrimaryItemProps } from "../types";
import { StyledNavPrimaryItem } from "./Style";
import SecondaryItem from "./SecondaryItem";

const CollapsedMenuNavButton = (props: INavPrimaryItemProps) => {
  const { item, isSelected, selectedId, handleItemSelect } = props;
  const subItemSelected = useSubItemSelected(item, selectedId);
  const buttonLabel = item.icon ? undefined : item.label[0].toUpperCase();
  const buttonVariant = isSelected || subItemSelected ? "primary" : "secondary";
  const handleClick = (e?: React.MouseEvent<HTMLButtonElement, MouseEvent>) => {
    e?.preventDefault();
    e?.stopPropagation();
    item.handleClick && item.handleClick(e);
    handleItemSelect(item);
  };
  return (
    <Button
      onClick={handleClick}
      icon={item.icon}
      tooltip={item.label}
      value={buttonLabel}
      variant={buttonVariant}
    />
  );
};

const commonId = "navPrimaryItem";

const NavPrimaryItem = React.memo((props: INavPrimaryItemProps) => {
  const { item, showLeadingIcon, handleItemSelect } = props;
  const { label, icon, subItems, id } = item;
  const subItemSelected = useSubItemSelected(item, props.selectedId);
  const { canExpand, isExpanded, toggleExpand } = useExpandItem(
    item,
    subItemSelected,
    props.selectedId
  );

  const handleClick = (e: React.MouseEvent<HTMLLIElement, MouseEvent>) => {
    e.preventDefault();
    e.stopPropagation();
    item.handleClick && item.handleClick(e);
    canExpand && !isExpanded && toggleExpand();
    handleItemSelect(item);
  };

  if (props.isMenuCollapsed) return <CollapsedMenuNavButton {...props} />;
  else
    return (
      <StyledNavPrimaryItem
        {...props}
        onClick={handleClick}
        isExpanded={isExpanded}
        id={commonId + "-" + id}
      >
        <div className={commonId}>
          {showLeadingIcon && (
            <div className="iconWrapper">{icon && <Icon icon={icon} />}</div>
          )}
          <Text
            value={label}
            variant={"h5"}
            color={subItemSelected ? "primary" : undefined}
          />
          {canExpand && (
            <div
              id={commonId + "-" + id + "-collapseButton"}
              onClick={(e) => {
                if (subItemSelected) {
                  e.stopPropagation();
                  e.preventDefault();
                }
              }}
            >
              <Button
                icon={isExpanded ? faChevronUp : faChevronDown}
                spacing="xs"
                outline
                disabled={isExpanded && subItemSelected}
                onClick={(e) => {
                  e?.preventDefault();
                  e?.stopPropagation();
                  toggleExpand();
                }}
              />
            </div>
          )}
        </div>
        {isExpanded && subItems && (
          <ul>
            {subItems.map((subItem) => (
              <SecondaryItem
                key={subItem.id}
                item={subItem}
                handleItemSelect={handleItemSelect}
                showLeadingIcon={showLeadingIcon}
                isSelected={props.selectedId === subItem.id}
              />
            ))}
          </ul>
        )}
      </StyledNavPrimaryItem>
    );
});

NavPrimaryItem.displayName = "NavPrimaryItem";
export { NavPrimaryItem };
export default NavPrimaryItem;
