import { useState, useEffect } from "react";
import merge from "lodash/merge";
import Highcharts from "highcharts";
import { Theme } from "@faharmony/core";

// Override options

const axisOptions: Highcharts.XAxisOptions = {
  tickColor: Theme.colorText("secondary"),
  lineColor: Theme.colorText("secondary"),
  gridLineColor: Theme.colorOutline(),
  minorGridLineColor: Theme.colorOutline(),
  labels: {
    useHTML: true,
    style: {
      fontFamily: Theme.fontName(),
      color: Theme.colorText("secondary"),
    },
  },
  title: {
    useHTML: true,
    style: {
      fontFamily: Theme.fontName(),
      color: Theme.colorText(),
    },
  },
};

const exportingOptions: Highcharts.ExportingOptions = {
  buttons: {
    contextButton: {
      symbol: "export",
      symbolFill: Theme.colorText("secondary"),
      symbolStroke: "transparent",
      theme: {
        fill: "transparent",
        stroke: Theme.colorOutline(),
      },
      menuItems: [
        "printChart",
        "downloadPNG",
        "downloadJPEG",
        "downloadPDF",
        "downloadSVG",
      ],
    },
  },
};

const legendOptions: Highcharts.LegendOptions = {
  itemStyle: {
    fontFamily: Theme.fontName(),
    fontWeight: "bold",
    fontSize: "0.75rem",
    color: Theme.colorText("secondary"),
  },
  itemHoverStyle: {
    color: Theme.colorText(),
  },
};

const tooltipOptions: Highcharts.TooltipOptions = {
  borderColor: Theme.colorOutline(),
  backgroundColor: Theme.colorBackground("elevated"),
  borderRadius: 8,
  style: {
    fontFamily: Theme.fontName(),
    color: Theme.colorText("secondary"),
  },
  useHTML: true,
};

const chartOptions: Highcharts.ChartOptions = {
  backgroundColor: "transparent",
  className: "chart",
  style: {
    fontFamily: Theme.fontName(),
    color: Theme.colorText(),
  },
};

const titleOptions: Highcharts.TitleOptions = {
  align: "left",
  useHTML: true,
  margin: 32,
  style: {
    fontFamily: Theme.fontName(),
    fontWeight: "bold",
    fontSize: "1rem",
    color: Theme.colorText(),
  },
  y: 16,
  x: 0,
  widthAdjust: -32,
};

const subtitleOptions: Highcharts.SubtitleOptions = {
  align: "left",
  useHTML: true,
  style: {
    fontFamily: Theme.fontName(),
    fontSize: "1rem",
    color: Theme.colorText("secondary"),
  },
  y: 48,
  x: 0,
};

const creditsOptions: Highcharts.CreditsOptions = {
  enabled: false,
};

const langOptions: Highcharts.LangOptions = {};

export const useChartOptions = (customOptions: Highcharts.Options) => {
  const defaultOptions: Highcharts.Options = {
    legend: {
      enabled: true,
    },
  };
  const overrideOptions: Highcharts.Options = {
    colors: [
      "#058DC7",
      "#50B432",
      "#ED561B",
      "#DDDF00",
      "#24CBE5",
      "#64E572",
      "#FF9655",
      "#FFF263",
      "#6AF9C4",
    ],
    lang: langOptions,
    credits: creditsOptions,
    title: titleOptions,
    subtitle: subtitleOptions,
    chart: chartOptions,
    tooltip: tooltipOptions,
    legend: legendOptions,
    xAxis: axisOptions,
    yAxis: axisOptions,
    exporting: exportingOptions,
  };
  const mergeOptions = (opts: Highcharts.Options) =>
    merge(defaultOptions, opts, overrideOptions);
  // Options state
  const [options, setOptions] = useState(mergeOptions(customOptions));
  useEffect(() => {
    setOptions(mergeOptions(customOptions));
  }, [customOptions]);
  // if (process.env.NODE_ENV === "development")
  //   console.info(
  //     "Chart options (not logged in PROD) - " + options.title?.text,
  //     options
  //   );

  return options;
};
