import React from "react";
import { Grid, useResponsive, useToast, useNetwork } from "@faharmony/core";
import { IRenderProps } from "../types";
import { useCheckUserForRoles, combineModuleRoles } from "../helpers";
import { FABar } from "../views";
import AppNavigation from "./Navigation";
import MainView from "./MainView";
import SideDrawer, { setAppSideDrawer } from "./SideDrawer";
import useGlobalActions from "./useGlobalActions";
import useAppSwitcher from "./useAppSwitcher";

const App = (props: IRenderProps) => {
  const { devMode, multilingual, Modules } = props;
  const { isMobile } = useResponsive();
  const { addToast, removeToast } = useToast();
  const apps = useAppSwitcher(devMode);
  const actions = useGlobalActions(multilingual, devMode);
  const isAllowedAnything = useCheckUserForRoles(combineModuleRoles(Modules));

  useNetwork({
    onOffline: () =>
      addToast({
        id: "offline",
        title: "No internet connection",
        variant: "warning",
        persist: true,
      }),
    onOnline: () => removeToast("offline"),
  });

  return (
    <Grid
      spacing="none"
      gridTemplate={isMobile ? "52px auto / auto" : "auto / 52px auto"}
    >
      <FABar apps={apps} actions={actions} />
      <Grid
        overflow="hidden"
        gridTemplate={"auto / max-content auto max-content"}
        spacing="none"
        // animate
      >
        {/* App Navigation */}
        {isAllowedAnything ? (
          <AppNavigation {...props} />
        ) : (
          <div id="appNavigation" />
        )}

        {/* Primary View */}
        <MainView {...props} />

        {/* Side View */}
        <SideDrawer />
      </Grid>
    </Grid>
  );
};

export { setAppSideDrawer };
export default App;
