import styled from "@emotion/styled";
import { Theme } from "@faharmony/core";

interface StyledProps {
  headless?: boolean;
}

export const StyledChart = styled.div<StyledProps>`
  position: relative;
  background: ${(props) =>
    props.headless ? "none" : Theme.colorBackground("elevated")};
  border-radius: ${(props) => (props.headless ? 0 : Theme.radius())};
  /* margin: ${(props) => (props.headless ? 0 : Theme.spacing())}; */
  border: 1px solid
    ${(props) => (props.headless ? "none" : Theme.colorOutline("border"))};
  box-shadow: ${(props) => (props.headless ? "none" : Theme.shadow("default"))};
  overflow: hidden;
  height: 100%;

  a {
    color: currentColor;
  }

  .header {
    position: absolute;
    top: 0px;
    left: 0;
    right: 0;
    z-index: 0;
    height: 40px;    
    background: ${(props) =>
      props.headless ? "transparent" : Theme.colorBackground("base")};
    border-bottom: 1px solid
      ${(props) => (props.headless ? "transparent" : Theme.colorOutline())};
  }

  div:nth-of-type(2) {
    height: 100%;
  }

  .highcharts-title {
    overflow: hidden;
    white-space: ${(props) =>
      props.headless ? "initial" : "nowrap"} !important;
  }

  .highcharts-root {
    height: 100%;
  }

  .highcharts-button-box {
    fill: transparent !important;
    width: 28px;
    height: 28px;
    transform: translate(-4px, -5px);
  }

  .highcharts-button {
    text {
      color: unset !important;
      fill: unset !important;
    }
  }

  .highcharts-button-symbol {
    transform: translate(-2px, -3px);
  }

  .highcharts-button-box {
    stroke: none;
  }

  .highcharts-label {
    text {
      color: unset !important;
      fill: unset !important;
    }
    &.highcharts-drilldown-data-label {
      text {
        color: ${() => Theme.colorTint("blue")} !important;
        fill: ${() => Theme.colorTint("blue")} !important;
      }
    }

    .highcharts-text-outline {
      stroke: transparent !important;
      fill: transparent !important;
    }
  }

  .highcharts-menu {
    background: ${() => Theme.colorBackground("elevated")} !important;
    border: 1px solid ${() => Theme.colorOutline()} !important;
    border-radius: ${() => Theme.radius("sm")} !important;
    box-shadow: ${() => Theme.shadow("hover")} !important;

    .highcharts-menu-item {
      font-family: ${() => Theme.fontName()} !important;
      color: ${() => Theme.colorText("secondary")} !important;

      :hover {
        color: ${() => Theme.colorText()} !important;
        background: ${() => Theme.colorBackground("hover")} !important;
      }
    }
  }
`;
