import React from "react";
import {
  faSquare,
  faMinusSquare,
  faCheckSquare,
} from "@fortawesome/pro-light-svg-icons";
import { TableToggleCommonProps2 } from "react-table";
import { Icon } from "@faharmony/core";

type InputRef =
  | React.RefObject<HTMLInputElement>
  | React.MutableRefObject<HTMLInputElement | undefined>;
export function isRef(
  ref: ((instance: HTMLInputElement | null) => void) | InputRef
): ref is InputRef {
  return (ref as InputRef).current !== undefined;
}

const Checkbox = (props: TableToggleCommonProps2) => {
  const { indeterminate, checked, onChange, style, title } = props;
  const icon = (
    <Icon
      icon={indeterminate ? faMinusSquare : checked ? faCheckSquare : faSquare}
    />
  );
  const handleClick = (event: React.MouseEvent<HTMLDivElement, MouseEvent>) => {
    event.stopPropagation();
    onChange && onChange(event);
  };
  return (
    <div onClick={handleClick} style={style} id={`table-checkbox-${title}`}>
      {icon}
    </div>
  );
};

export default Checkbox;
