import React from "react";
import { Text, Theme } from "@faharmony/core";
import { ColumnFilters, TableView } from "@faharmony/table";
import {
  faPlus,
  faTrash,
  faEdit,
  faUpload,
} from "@fortawesome/pro-light-svg-icons";

