const range = (len: number) => {
  const arr = [];
  for (let i = 0; i < len; i++) {
    arr.push(i);
  }
  return arr;
};

const newPerson = () => {
  const statusChance = Math.random();
  return {
    firstName: "zyx",
    lastName: "lol",
    age: Math.floor(Math.random() * 30),
    visits: Math.floor(Math.random() * 100),
    progress: Math.floor(Math.random() * 100),
    status:
      statusChance > 0.66
        ? "relationship"
        : statusChance > 0.33
        ? "complicated"
        : "single",
    status2:
      statusChance > 0.66
        ? "relationship"
        : statusChance > 0.33
        ? "complicated"
        : "single",
    status3:
      statusChance > 0.66
        ? "relationship"
        : statusChance > 0.33
        ? "complicated"
        : "single",
    status4:
      statusChance > 0.66
        ? "relationship"
        : statusChance > 0.33
        ? "complicated"
        : "single",
  };
};

function makeData(...lens: any) {
  const makeDataLevel = (depth = 0): any => {
    const len = lens[depth];
    return range(len).map(() => {
      const val = {
        ...newPerson(),
        subRows: lens[depth + 1] ? makeDataLevel(depth + 1) : undefined,
      };
      return val;
    });
  };

  return makeDataLevel();
}

export const Table = () => {
  const data = React.useMemo(() => makeData(29, 2), []);

  const columns = React.useMemo(
    () => [
      {
        Header: "First Name",
        accessor: "firstName",
        width: 200,
        Cell: (data: any) => (
          <Text value={data.row.original.firstName + "..."} />
        ),
      },
      {
        Header: "Last Name",
        accessor: "lastName",
        filter: "fuzzyText",
        disableFilters: true,
        disableSortBy: true,
      },
      {
        Header: "Age",
        accessor: "age",
        Filter: ColumnFilters.NumberRangeColumnFilter,
        filter: "between",
        disableFilters: true,
        width: 80,
      },
      {
        Header: "Visits",
        accessor: "visits",
        Filter: ColumnFilters.NumberRangeColumnFilter,
        filter: "between",
      },
      {
        Header: "Progress",
        accessor: "progress",
        Filter: ColumnFilters.SliderColumnFilter,
      },
      {
        Header: "Status",
        accessor: "status",
        Filter: ColumnFilters.SelectColumnFilter,
        filter: "includes",
      },
      {
        Header: "Status2",
        accessor: "status2",
        Filter: ColumnFilters.SelectColumnFilter,
        filter: "includes",
        hidden: true,
      },
      {
        Header: "Status3",
        accessor: "status3",
        Filter: ColumnFilters.SelectColumnFilter,
        filter: "includes",
        hidden: true,
      },
      {
        Header: "Status4",
        accessor: "status4",
        Filter: ColumnFilters.SelectColumnFilter,
        filter: "includes",
        hidden: true,
      },
    ],
    []
  );

  const deleteButton = (props: any) => ({
    id: "delete",
    icon: faTrash,
    tooltip: "Delete",
    onClick: () => console.log(props),
  });
  const editButton = (props: any) => ({
    id: "edit",
    icon: faEdit,
    tooltip: "Edit",
    onClick: () => console.log(props),
  });

  const tableActions = [
    {
      id: "add",
      onClick: console.log,
      icon: faPlus,
      label: "Add",
    },
    {
      id: "upload",
      onClick: console.log,
      icon: faUpload,
      tooltip: "Upload",
    },
  ];

  const SubComponent = (row: any) => (
    <div style={{ padding: Theme.spacing("xl"), width: "inherit" }}>
      <h4>Row SubComponent</h4>
      <br />
      Render any thing related to the row without any subRows.
      <br />
      <pre>
        {Object.entries(row).map(([key, val]) => (
          <div>
            {key}: {val}
          </div>
        ))}
      </pre>
    </div>
  );

  const [pagination] = React.useState(true);
  const [sorting] = React.useState(true);
  const [filtering] = React.useState(true);
  const [exporting] = React.useState(true);
  const [columnSelect] = React.useState(true);
  const [multiRow] = React.useState(true);

  return (
    <TableView
      tableHeading="TableView"
      columns={columns}
      data={data}
      handleRowClick={(row) => console.log("click" + row)}
      handleRowDblClick={(row) => console.log("dblClick" + row)}
      tableActions={tableActions}
      multipleRowActions={(rows) => [deleteButton(rows)]}
      singleRowActions={(row) => [editButton(row), deleteButton(row)]}
      enableMultipleRowSelect={multiRow}
      enableSorting={sorting}
      enablePagination={pagination}
      enableFiltering={filtering}
      exportTable={exporting}
      enableColumnVisibilitySelector={columnSelect}
      renderRowSubComponent={SubComponent}
    />
  );
};
