import React from "react";
import { v1 as nextId } from "uuid";
import { Column } from "react-table";
import { TableChildrenProps } from "../types";

const isFooterEmpty = <T extends object>(columns: Column<T>[]): boolean => {
  let empty = true;
  columns.forEach((column) => {
    if (column.Footer !== undefined) empty = false;
  });
  return empty;
};

export const TableFooter = <T extends object>({
  tableInstance,
  tableProps,
}: TableChildrenProps<T>) => {
  const { columns } = tableProps;
  const { footerGroups } = tableInstance;

  if (isFooterEmpty<T>(columns)) return null;

  return (
    <tfoot key={nextId()}>
      {footerGroups.map((group) => (
        <tr {...group.getFooterGroupProps()} key={nextId()}>
          {group.headers.map((column) => (
            <td {...column.getFooterProps()} key={nextId()}>
              {column.render("Footer")}
            </td>
          ))}
        </tr>
      ))}
    </tfoot>
  );
};

export default TableFooter;
