import { FAChartProps } from "./type";
import { Area } from "./variants/Area";
import { Bar } from "./variants/Bar";
import { Line } from "./variants/Line";
import { Pie } from "./variants/Pie";

// PORTALS: https://codesandbox.io/s/1o5y7r31k3
// Create Chart.Line, Chart.Pie, etc.
/**
 * @author Siddhant Gupta <siddhant@fasolutions.com>
 *
 * ---
 */
const Chart = (props: FAChartProps) => {
  if (!Highcharts)
    return <span>Library Highcharts is missing. Install the NPM library.</span>;
  return <Line {...props} />;
};

Chart.Line = Line;
Chart.Bar = Bar;
Chart.Area = Area;
Chart.Pie = Pie;
export { Chart };
export default Chart;
